<template>
    <div class="toaster-page-container">
        <div class="toaster-container">
            <span class="toast-content">
                {{content}}
            </span>
        </div>
    </div>
    
</template>

<script>
    export default {
        data(){
            return {
                content:''
            }
        },
        mounted(){
            let {title}  = this.$attrs;
            this.content = title;
        }
    }
</script>

<style scoped>
    .toaster-page-container {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 1800;
        touch-action: none;
        pointer-events: none;
    }
    .toaster-container {
        display: inline-block;
        height: 80rpx;
        padding: 0 1.5em;
        background: #000;
        opacity: 0.74;
        touch-action: none;
        pointer-events: none;
        border-radius: 25rpx
    }
    .toast-content {
        color: #fff;
        line-height: 80rpx;
    }
</style>