import Vue from 'vue'
import Router from 'vue-router'
import Canvas from '../pages/index/Canvas.vue'

Vue.use(Router)

const routes = [
    {
        path: '/index',
        component: Canvas
    },{
        path: '/',
        redirect: '/index'
    }
]

const router = new Router({
    mode:'history',
    routes
})

export default router