<!-- pixfont.vue 像素字体组件 -->
<template>
	<div class="pixfont-box" v-if="fonts.length" :style="{'fontSize':fontSize+'px', 'lineHeight':fontSize+'px'}">
        <svg v-for="(item, i) in fonts" :key="i" :width="item.width" :height="item.height" :viewBox="'0 0 '+item.width+' '+item.height" fill="none" xmlns="http://www.w3.org/2000/svg" :style="{'width':fontSize*item.width/10+'px','height':fontSize*item.height/10+'px','margin':'0 '+fontSize*0.1+'px','verticalAlign':'bottom'}">
            <path :d="item.path" :fill="fontColor" />
        </svg>
    </div>
</template>

<script>
	export default {
        /* 参数：size： 像素字的大小，以rpx为单位；color：像素字的颜色 */
		props: {
            size:{},
            color:{
                type: String
            }
		},
		data(){
			return {
				fonts:[],
                prevText:''//用于保存上一个输入的字符串，只有当前输入的字符串与之前相比发生变化时才执行更新，避免死循环。
			}
		},
		watch:{
			
		},
		updated(){
            let newFont;
            if(this.$slots.default && this.$slots.default.length){
                newFont = this.$slots.default[0].text;
            }else{
                newFont = '';
            }
            let {prevText} = this;
            if(newFont != prevText){
                this.updateFontRender();
            }
        },
		mounted(){
            this.updateFontRender();
		},
		methods:{
			updateFontRender(){
                let font;
                if(this.$slots.default && this.$slots.default.length > 0){
                    font = this.$slots.default[0].text;
                }else{
                    font = '';
                }
                this.prevText = font;
                if(font){
                    let color = this.color || '#000';
                    let fontSize = this.size || 10;
                    if(typeof(fontSize) == 'string'){
                        fontSize = parseFloat(fontSize);
                    }
                    let screenWidth = this.$store.state.screenWidth;
                    fontSize = fontSize * screenWidth / 750;
                    this.fontSize = fontSize;
                    this.fontColor = color;
                    //去除空格后，将得到的文本内容中可能含有的中文转换成utf-8
                    font = font.replace(/ /g, '');
                    font = this.$u.toUtf8(font);
                    font = font.toLowerCase();
                    //将处理完毕的字符串中的每一个单字都拆分出来并push到数组中
                    let fontArr = [];
                    for(let i = 0;i < font.length;i++){
                        let letter = font.substr(i, 1);
                        fontArr.push(letter);
                    }
                    //获取svg路径信息并存入状态
                    let fonts = [];
                    for(let i = 0;i < fontArr.length;i++){
                        let oFont = this.$pix[fontArr[i]];
                        fonts.push(oFont);
                    }
                    this.fonts = fonts;
                }
            }
		}
	}
</script>

<style scpoed>
	.pixfont-box {
		display: inline-block;
	}
</style>