<template>
  <div :class="'menu-item-container '">
	<div class="menu-item-title" @click.stop="handleTitleClick" :style="{'color':fontColor}">{{title}}</div>
	<div class="submenu-container" :style="{'maxHeight':submenu?15*submenuCount+'vw':'0'}">
		<slot></slot>
	</div>
  </div>
</template>

<script>
export default {
  props:{
	name: {
		type: String
	},
	showSubmenu: {
		type: Boolean
	},
	color: {
		type: String
	}
  },
  data() {
    return {
		title:'',
		submenu: true,
		submenuCount: 0,
		fontColor: '#000'
    };
  },
  watch:{
	showSubmenu:function(val){
		this.submenu = val;
	}
  },
  mounted(){
	let {showSubmenu, color} = this;
	if(color){
		this.fontColor = color;
	}
	this.title = this.name;
	this.submenu = showSubmenu;
	let submenuList = this.$slots.default;
	if(submenuList){
		this.submenuCount = submenuList.length;
	}else{
		this.submenuCount = 0;
	}
  },
  methods: {
	handleTitleClick(e){
		this.$emit('click',e);
	}
  }
}
</script>

<style scoped>
  .menu-item-container{
	border-bottom: rgba(0,0,0,0.1) 4rpx solid;
	touch-action: none;
  }
  .menu-item-title{
	width: 100%;
	height: 84rpx;
	line-height: 84rpx;
	font-weight: 600;
	font-size: 32rpx;
	transition: background .3s;
  }
  .menu-item-title:active {
	background: #f4f4f4
  }
  .submenu-container {
	overflow: hidden;
	transition: max-height .4s
  }
  
</style>