<!-- 使用了mask的组件中请用@click.stop绑定点击事件，防止事件冒泡 -->
<template>
  <div class="pixcell-mask" @click.stop="handleClick" :style="{'zIndex':zIndex}">
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
    //遮罩的层级。层级越高，z-index越大，遮罩层越在表面
	level: {
		required: false
	}
  },
  data() {
    return {
      zIndex:0
    }
  },
  watch:{
	level: function(val){
		this.updateZIndex(val);
		return val;
	}
  },
  mounted(){
	this.updateZIndex();
  },
  methods: {
	handleClick(e){
		this.$emit('click',e);
		return false;
	},
	updateZIndex(v){
		let lv;
		if(!v){
			lv = this.level;
		}else{
			let lv = v;
		}
		lv = parseInt(lv);
		if(!lv){
			lv = 0;
		}
		this.zIndex = lv;
	}
  }
}
</script>

<style scoped>
  .pixcell-mask{
    position:fixed;
	left: 0;
	top: 0;
	right:0;
	bottom:0;
	background: rgba(0, 0, 0, 0.5);
	display: -webkit-flex;
	display: -moz-flex;
	display: -o-flex;
	display:flex;
	justify-content:center;
	align-items:center;
  }
</style>