import Vue from 'vue'
import {VueJsonp} from 'vue-jsonp'
import App from './App.vue'
import store from '@/common/store'
import api from '@/common/utils/request.js'
import * as commonHelper from '@/common/utils/commonHelper.js'
import pixcellFontSvg from '@/common/utils/pixcellFontSvg.js'
import router from '@/router/index.js'

Vue.use(VueJsonp)
Vue.config.productionTip = false

Vue.prototype.$u = commonHelper
Vue.prototype.$pix = pixcellFontSvg
Vue.prototype.$api = api
Vue.prototype.jsp = function(url,data){
	url += '?';
	// jsonp请求参数和get方法类似，都是params的方式拼接
	if(data && typeof(data) == 'object'){
		for(let item in data){
			url += `&${item}=${data[item]}`;
		}
	}
	return this.$jsonp(url);
}

App.mpType = 'app'

router.beforeEach((to, from, next) => {
  if(to.meta.title){
    document.title = to.meta.title;
  }
  next();
})

new Vue({
  store,
  router,
  render: h => h(App)
}).$mount('#app')
