						<div class="help-description-box">
							<div style="width: 100%">
								<p class="small-raw">左右滑动缩放条</p>
								<p class="small-raw">对画布进行放大缩小操作</p>
								<p class="small-raw">双指操作也可缩放、移动画布</p>
