					<div class="little-circle" :style="{'background':currentPage==1?'#fff':'#000'}"></div>
					<div class="little-circle" :style="{'background':currentPage==2?'#fff':'#000'}"></div>
					<div class="little-circle" :style="{'background':currentPage==3?'#fff':'#000'}"></div>
				</div>
			</div>
		</div>
	</pix-mask>
</template>

<script>
	import pixMask from '../containers/pixcellMask.vue'
	export default {
		components:{
			pixMask
		},
		data(){
			return {
                pv1: 0,
				pv2: 0,
				handLeft1: 5,
				handTop1: 40.4,
				ptLeft: -5.334,
				ptTop: 26,
				currentPage: 1,
				oX: 0,
				didTurnPage: false,
				showPlatter: false,
				showColorCard: false,
				helpWindowHeight: 35.2
			}
		},
		watch:{
			currentPage(p){
				if(p==1){
					this.doAnimate1();
				}else{
					this.pv1 = 0;
					this.pv2 = 0;
					this.handLeft1 = 5;
					this.handTop1 = 40.4;
					this.ptLeft = -5.334;
					this.ptTop = 26;
				}
			}
		},
		mounted(){
			let intervalId = setInterval(() => {
				this.doAnimate1();
				clearInterval(intervalId);
			},50)
		},
		methods:{
			//执行第一页的动画
			doAnimate1(){
				this.pv1 = 100;
				this.pv2 = 100;
				this.handLeft1 = 41.8;
				this.handTop1 = 20.53;
				this.ptLeft = 23.46667;
				this.ptTop = 10;
			},
			//帮助窗口触摸事件
			helperTouchstart(et){
				let tX = et.touches[0].clientX;
				this.oX = tX;
				return false;
			},
			//触摸移动事件
			helperTouchmove(em){
				if(!this.didTurnPage){
					let mX = em.touches[0].clientX;
					let {oX} = this;
					let dX = mX-oX;
					if(dX >= 128){
						//判定，向左翻页
						this.didTurnPage = true;
						if(this.currentPage >= 2){
							this.currentPage--;
						}else{
							//已经到头了，做一个小动画表示已经到头
							this.currentPage -= 0.15;
							let timeoutId = setTimeout(() => {
								this.currentPage = 1;
								clearTimeout(timeoutId);
							}, 320);
						}
					}else if(dX <= -128){
						//判定，向右翻页
						this.didTurnPage = true;
						if(this.currentPage <= 2){
							this.currentPage++;
						}else{
							//已经到头了
							this.currentPage += 0.15;
							let timeoutId = setTimeout(() => {
								this.currentPage = 3;
								clearTimeout(timeoutId);
							}, 320);
						}
					}
				}
				return false;
			},
			//触摸结束事件
			helperTouchend(){
				this.didTurnPage = false;
				this.oX = 0;
			},
			switchFrametabToPlatter(){
				this.showPlatter = true;
				this.showColorCard = false;
				this.helpWindowHeight = 66.13333;
				return false;
			},
			switchFrametabToCard(){
				this.showPlatter = false;
				this.showColorCard = true;
				this.helpWindowHeight = 59.73333;
				return false;
			},
			//退出帮助
			quithelp(){
				this.$emit('cancel');
				return false;
			}
		}
	}
</script>

