<template>
	<pix-mask level="999">
		<div class="warning-container">
			<div class="header">{{title}}</div>
			<slot></slot>
            <div class="footer-btns">
                <div class=" btn btn-cancel" @click.stop="handleCancel">取消</div>
				<div class="btn btn-confirm" @click.stop="handleConfirm">确定</div>
            </div>
		</div>
	</pix-mask>
</template>

<script>
	import pixMask from '../containers/pixcellMask.vue'
	export default {
		components:{
			pixMask
		},
		data(){
			return {
                title:''
			}
		},
		mounted(){
            let title = this.$attrs.title;
            if(!title){
                title = '警告';
            }
            this.title = title;
		},
		methods:{
			handleCancel(){
                this.$emit('cancel');
            },
            handleConfirm(){
                this.$emit('confirm');
            }
		}
	}
</script>

<style scoped>
	@media screen and (max-width:750px){
		/* 移动端 */
        .warning-container {
            width: 490rpx;
        }
	}
	@media screen and (min-width:751px){
		/* 电脑端 */
        .warning-container {
            width: 320px;
        }
	}
	.warning-container{
		background: #fff;
		border-radius:24rpx;
		border: #000 4rpx solid;
		overflow: hidden;
		z-index: 1100;
		box-shadow: 4rpx 4rpx 0px #000;
	}
    .header {
        width:100%;
		height:6vh;
		background: #fff;
		color: #000;
		text-align:center;
		line-height: 6vh;
		font-weight: bold;
    }
    .footer-btns{
		border-top: #000 3rpx solid;
	}
    .btn{
		height: 90rpx;
		width: 49%;
		display: inline-block;
		text-align: center;
		line-height:90rpx;
		cursor: pointer;
	}
	.btn-cancel{
		color: #aaa;
		border-right: #000 3rpx solid
	}
	.btn-confirm{
		color: #000;
		font-weight: 700
	}
</style>
