<template>
	<div class="dragger-container">
		<div class="pixcell-dragger-box" @mousedown="handleMouseDown" @touchstart="handleTouchStart" @touchmove="handleTouchMove"  ref="dragger">
			<div class="drag-pointer" :style="{'left':'calc('+xPos+'% - 0.93333333vw)'}"></div>
			<div class="progress-bar" :style="{'width':xPos+'%'}"></div>
			<div class="slide-bar"></div>
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			value:{
				type: Number
			},
		},
		data(){
			return {
				isApp: this.$store.state.isApp,
				newValue: 0,
				isInt: false,
			}
		},
		watch:{
			value:function(v){
				this.newValue = v;
			},
			newValue:function(v){
				this.$emit('input', v);
			}
		},
		computed:{
			xPos:{
				get:function(){
					let newValue = this.newValue;
					let max = this.maxVal;
					let min = this.minVal;
					if(min == undefined || max == undefined){
						return 0;
					}
					return (newValue-min) / (max-min) * 100;
				},
				set:function(xPos){
					let min = this.minVal;
					let max = this.maxVal;
					let dis = max - min;
					let nv = min + xPos / 100 * dis;
					if(this.isInt){
						nv = Math.round(nv);
					}
					nv = this.$u.limitVal(nv, min, max);
					this.newValue = nv;
				}
			}
		},
		mounted(){
			this.initBar();
		},
		methods:{
			initBar(){
				this.barWidth = this.$refs.dragger.clientWidth;
				let minVal = this.$attrs.minVal;
				let maxVal = this.$attrs.maxVal;
				let isInt = this.$attrs.isInt;
				if(minVal == undefined){
					minVal = 0;
				}else if(typeof(minVal) == 'string'){
					minVal = parseFloat(minVal) || 0;
				}
				if(maxVal == undefined){
					maxVal = 0;
				}else if(typeof(maxVal) == 'string'){
					maxVal = parseFloat(maxVal) || 0;
				}
				if(isInt == 'true' || isInt == true){
					this.isInt = isInt;
				}
				this.minVal = minVal;
				this.maxVal = maxVal;
				this.newValue = this.value;
			},
			handleMouseDown(ed){
				if(!this.isApp){
					
				}
			},
			handleTouchStart(et){
				let tX = et.touches[0].pageX - et.currentTarget.offsetLeft;
				let xPos = this.calcXPos(tX);
				this.xPos = xPos;
				return false;
			},
			handleTouchMove(em){
				let mX = em.touches[0].pageX - em.currentTarget.offsetLeft;
				let xPos = this.calcXPos(mX);
				this.xPos = xPos;
				return false;
			},
			calcXPos(x){
				let xPos = x / this.barWidth * 100;
				xPos = this.$u.limitVal(xPos, 0, 100);
				return xPos;
			}
		}
	}
</script>

<style scpoed>
	.dragger-container {
		width: 100%;
		display: flex;
		display: --webkit-flex;
		align-items: center;
		justify-content: center;
	}
	.pixcell-dragger-box {
		display: flex;
		display: --webkit-flex;
		width: 90%;
		height: 50rpx;
		align-items: center;
		position: relative;
	}
	.slide-bar {
		height:6rpx;
		background: #ccc;
		width: 100%;
		z-index: 0;
		border-radius: 3rpx
	}
	.drag-pointer {
		position: absolute;
		width: 14rpx;
		height:14rpx;
		border-radius: 7rpx;
		z-index: 9;
		background: #000
	}
	.progress-bar {
		background: #000;
		height: 6rpx;
		position: absolute;
		left: 0;
		z-index: 1;
		border-radius: 3rpx
	}
</style>
