//commonHelper.js

/**
 * 深拷贝
 * @param {Array} prevData
 */
function deepcopy(prevData){
	var r = prevData.length;
	var newData = [];
	for(let i = 0;i < r;i++){
		var raw = [];
		for(let j = 0;j < r;j++){
			raw.push(prevData[i][j]);
		}
		newData.push(raw);
	}
	return newData;
}
/**
 * 十六进制颜色转换成十进制数组
 * @param {String} h
 */
function toDec(h){
	if(h && (h.length == 3 || h.length == 6) && h.indexOf('#') == -1){
		h = '#' + h;
	}
	if(h && h.length == 4){
		//输入值是简写十六进制颜色
		var hR = '0x' + h.substr(1,1) + h.substr(1,1);
		var hG = '0x' + h.substr(2,1) + h.substr(2,1);
		var hB = '0x' + h.substr(3,1) + h.substr(3,1);
	}else if(h && h.length == 7){
		//输入值是标准十六进制颜色
		var hR = '0x' + h.substr(1,2);
		var hG = '0x' + h.substr(3,2);
		var hB = '0x' + h.substr(5,2);
	}else{
		return false;
	}
	hR = parseInt(hR) || 0;
	hG = parseInt(hG) || 0;
	hB = parseInt(hB) || 0;
	return [hR, hG, hB];
}
/**
 * 十六进制颜色书写规范化
 * @param {*} vR
 * @param {*} vG
 * @param {*} vB
 * @param {String} type
 */
function regColor(vR, vG, vB, type){
	if(type == 'num'){
		vR = vR.toString(16);
		vG = vG.toString(16);
		vB = vB.toString(16);
	}
	if(vR.length == 1){
		vR = '0' + vR;
	}
	if(vG.length == 1){
		vG = '0' + vG;
	}
	if(vB.length == 1){
		vB = '0' + vB;
	}
	return '#' + vR + vG + vB;
}
