<template>
	<div class="pixcell-mask">
		<div class="modal-container">
			<div class="header">调色板</div>
			<div class="main-ui">
				<div class="platter-box">
					<img class="aimer" src="../../static/icons/pointers/aimer.png" :style="{'left':pX-5+'px','top':pY-5+'px'}">
					<canvas id="platter" canvas-id="platter"  @mouseup="handleMouseUpA"></canvas>
					<canvas id="colorBar" canvas-id="colorBar"  @mouseup="handleMouseUpB"></canvas>
				</div>
				<div class="options-box">
					<div class="viewer">
						<div class="viewer-up" :style="{'background':defColor}"></div>
						<div class="viewer-down" :style="{'background':rgbHex}"></div>
					</div>
					<div class="params">
						<div class="option-line">
							<div class="option-text">当前RGB颜色参数</div>
						</div>
						<div class="option-line">
							<div class="option-text">红色</div>
							<input type="number" @blur="changeR" :value="rgbR" class="inpt" ref="R">
							<div class="option-text">绿色</div>
							<input type="number" @blur="changeG" :value="rgbG" class="inpt" ref="G">
							<div class="option-text">蓝色</div>
							<input type="number" @blur="changeB" :value="rgbB" class="inpt" ref="B">
						</div>
						<div class="option-line">
							<div class="option-text">当前HSB颜色参数</div>
						</div>
						<div class="option-line">
							<div class="option-text">色相</div>
							<input type="number" @blur="changeH" :value="hsbH" class="inpt" ref="H">
							<div class="option-text">饱和度</div>
							<input type="number" @blur="changeS" :value="hsbS" class="inpt" ref="S">
							<div class="option-text">亮度</div>
							<input type="number" @blur="changeV" :value="hsbV" class="inpt" ref="V">
						</div>
						<div class="option-line">
							<div class="option-text">十六进制颜色</div>
						</div>
						<div class="option-line">
							<div class="option-text">初始 &ensp;{{defColor}}</div>
							<div class="option-text" style="marginLeft: 10px">当前</div>
							<input type="text" @blur="changeHEX" :value="rgbHex" class="inpt hex" ref="HEX">
						</div>
					</div>
				</div>
			</div>
			<div class="footer-btn">
				<div class=" btn btn-cancel" @click="handleCancel">取消</div>
				<div class="btn btn-confirm" @click="handleConfirm">确定</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return {
				isApp: this.$store.state.isApp,
				defColor: '#ffffff',
				hsbH: 0,
				hsbS: 100,
				hsbV: 100,
				rgbR: 255,
				rgbG: 0,
				rgbB: 0,
				rgbHex: '#ff0000',
				mousePressA: false,
				mousePressB: false
			}
		},
		watch:{
			hsbH:function(h){
				h = parseInt(h) || 0;
				h = this.$u.limitVal(h, 0, 359);
				let y = h / 359 * this.ch;
				this.initColorBar(y);
				this.initPlatter(h);
				this.updateCurColor(this.pX, this.pY);
				this.hsbH = h;
			},
			rgbHex:function(v){
				//为避免陷入无限循环，该方法不对hsbH进行更新，当手动输入值同步数据时，hsbH需单独更新一次
				let rgb = this.$u.toDec(v);
				this.rgbR = rgb[0];
				this.rgbG = rgb[1];
				this.rgbB = rgb[2];
				let hsb = this.rgb2hsb(rgb[0], rgb[1], rgb[2], 'num');
				this.hsbS = hsb[1];
				this.hsbV = hsb[2];
			},
		},
		mounted(){
			this.initCanvas();
			this.initData();
		},
		computed:{
			pX:{
				get:function(){
					//饱和度值变化会触发pointer的pX坐标联动变化
					/* if(this.hsbS == undefined || this.ch == undefined){
						return 0;
					} */
					return this.hsbS / 100 * this.ch;
				},
				set:function(pX){}
			},
			pY:{
				get:function(){
					//亮度变化会触发pointer的pY坐标联动变化
					/* if(this.hsbV == undefined || this.ch == undefined){
						return 0;
					} */
					return (100 - this.hsbV) / 100 * this.ch;
				},
				set:function(pY){}
			},
			bY:{
				get:function(){
					//bY的值只和色相有关，它只需要监听hsbH并响应其变化即可。
					return this.hsbH / 359 * this.ch;
				},
				set:function(y){
					let h = Math.round(y / this.ch * 360);
					//this.initColorBar(y);
					this.hsbH = h;
				}
			}
		},
		methods:{
			//保留一位小数
			tofix(num){
				let f = num.toFixed(1);
				return parseFloat(f);
			},
			initCanvas(){
				let that = this;
				const canvasA = document.getElementById('platter');
				const canvasB = document.getElementById('colorBar');
				//const ctxA = uni.createCanvasContext('platter');
				//const ctxB = uni.createCanvasContext('colorBar');
				const ctxA = canvasA.getContext('2d');
				const ctxB = canvasB.getContext('2d');
				canvasA.width = canvasA.clientWidth;
				canvasA.height = canvasA.clientHeight;
				canvasB.width = canvasB.clientWidth;
				canvasB.height = canvasB.clientHeight;
				this.ch = canvasA.clientHeight;
				this.bw = canvasB.clientWidth;
				this.canvasA = canvasA;
				this.canvasB = canvasB;
				this.ctxA = ctxA;
				this.ctxB = ctxB;
				this.initCanvasEvent();
			},
			//根据设备信息为画布对象初始化分配事件
			initCanvasEvent(){
				const canvasA = this.canvasA;
				const canvasB = this.canvasB;
				if(this.isApp){
					canvasA.ontouchstart = this.handleTouchStartA;
					canvasB.ontouchstart = this.handleTouchStartB;
					canvasA.ontouchmove = this.handleTouchMoveA;
					canvasB.ontouchmove = this.handleTouchMoveB;
				}else{
					canvasA.onmousedown = this.handleMouseDownA;
					canvasB.onmousedown = this.handleMouseDownB;
					canvasA.onmousemove = this.handleMouseMoveA;
					canvasB.onmousemove = this.handleMouseMoveB;
				}
			},
			initData(){
				let that = this;
				this.defColor = this.$attrs.defColor;
				let rgb = this.$u.toDec(this.defColor);
				let hsb = this.rgb2hsb(rgb[0], rgb[1], rgb[2]);
				let x = hsb[1] / 100 * this.ch;
				let y = (100 - hsb[2]) / 100 * this.ch;
				if(!hsb[0]){
					this.hsbH = 1;
				}else{
					this.hsbH = hsb[0];
				}
				this.updateCurColor(x, y);
				let timerId = setTimeout(() => {
					that.hsbS = 0;
					that.hsbV = 0;
					that.hsbS = hsb[1];
					that.hsbV = hsb[2];
					clearTimeout(timerId);
				}, 20)
			},
			//触屏环境下获取触点位置
			getTouchpoint(e){
				//计算触点在画布中的相对坐标
				var tp = e.touches[0];
				var tg = e.target.parentNode;
				return [tp.clientX-tg.offsetLeft, tp.clientY-tg.offsetTop];
			},
			rgb2hsb(r, g, b){
				let tofix = this.tofix;
				let rd = Math.round;
				let [max, min] = this.$u.findMaxAndMin([r, g, b]);
				let d = max - min;
				let h, s, v = max / 255 * 100;
				if(max == 0){
					s = 0;
				}else{
					s = (1 - min/max) * 100;
				}
				if(max == min){
					h = 0;
				}else if(max == r && g >= b){
					h = (g-b) * 60 / d;
				}else if(max == r && b > g){
					h = (g-b) * 60 / d + 360;
				}else if(max == g){
					h = (b-r) * 60 / d + 120;
				}else if(max == b){
					h = (r-g) * 60 / d + 240;
				}
				return [tofix(h), tofix(s), tofix(v)];
			},
			hsb2rgb(h, s, v){
				s = s / 100;
				v = v/ 100;
				let rd = Math.round;
				let r, g, b;
				let ar = h / 60;
				let hi = Math.floor(ar);
				let f = ar - hi;
				let p = v * (1 - s);
				let q = v * (1 - f*s);
				let t = v * (1 - (1-f)*s);
				switch(hi){
					case 0 :
						[r, g, b] = [v, t, p];
						break;
					case 1 :
						[r, g, b] = [q, v, p];
						break;
					case 2 :
						[r, g, b] = [p, v, t];
						break;
					case 3 :
						[r, g, b] = [p, q, v];
						break;
					case 4 :
						[r, g, b] = [t, p, v];
						break;
					case 5 :
						[r, g, b] = [v, p, q];
						break;
				}
				return [rd(r*255), rd(g*255), rd(b*255)];
			},
			/**
			 * 单次扫描绘制渐变线条
			 * @param {number} x1 y1 起始点横纵坐标
			 * @param {number} x2 y2 结束点横纵坐标
			 * @param {String} sc 起始点颜色
			 * @param {String} ec 结束点颜色
			 **/
			drawRadientLine(x1, y1, x2, y2, sc, ec){
				const ctxA = this.ctxA;
				ctxA.beginPath();
				var grd = ctxA.createLinearGradient(x1, y1, x2, y2);
				grd.addColorStop(0, sc);
				grd.addColorStop(1, ec);
				ctxA.strokeStyle = grd;
				ctxA.moveTo(x1, y1);
				ctxA.lineTo(x2, y2);
				ctxA.closePath();
				ctxA.stroke();
			},
			/**
			 * 根据当前色相扫描更新调色板
			 * @param {number} h 色相
			 */
			initPlatter(h){
				let ch = this.ch;
				let k = 256 / ch;
				for(let i = 0;i < ch;i++){
					var scv = Math.round(255-i*k);
					var v = scv / 255 * 100;
					var sc = this.$u.regColor(scv, scv, scv, 'num');
					var ecs = this.hsb2rgb(h, 100, v);
					var ec = this.$u.regColor(ecs[0], ecs[1], ecs[2], 'num');
					this.drawRadientLine(0, i, ch, i, sc, ec);
				}
			},
			/**
			 * 生成色相条 
			 * @param {number} y
			 * */
			initColorBar(y){
				let rd = Math.round;
				const ctxB = this.ctxB;
				let {bw, ch} = this;
				ctxB.beginPath();
				ctxB.clearRect(0, 0, bw, ch);
				var d = 360 / ch;
				for(let i = 0;i < ch; i++){
					ctxB.beginPath();
					ctxB.moveTo(0, i);
					ctxB.lineTo(bw, i);
					var h = i * d;
					var c = this.hsb2rgb(h, 100, 100);
					if(i == rd(y)){
						ctxB.strokeStyle = '#fff';
					}else{
						ctxB.strokeStyle = this.$u.regColor(c[0], c[1], c[2], 'num');
					} 
					ctxB.stroke();
				}
			},
			/**
			 * 获取点的颜色并更新数据
			 * @param {number} x
			 * @param {number} y
			 */
			updateCurColor(x, y){
				if(isNaN(x) || isNaN(y)){
					return;
				}
				let that = this;
				let ch = this.ch;
				x = x || 0;
				y = y || 0;
				//由于不同移动设备生成的调色板实际图像有误差，为了精确获取颜色值，不能直接使用接口获取，需要根据坐标值计算
				let s = this.tofix(x / ch * 100);
				let v = this.tofix((ch - y) / ch * 100);
				let h = this.hsbH;
				let rgb = this.hsb2rgb(h, s, v);
				this.rgbHex = this.$u.regColor(rgb[0], rgb[1], rgb[2], 'num');
			},
			//事件函数
			handleMouseDownA(ed){
				let ch = this.ch;
				let cX = ed.offsetX;
				let cY = ed.offsetY;
				cX = this.$u.limitVal(cX, 0, ch);
				cY = this.$u.limitVal(cY, 0, ch);
				this.pX = cX;
				this.pY = cY;
				this.mousePressA = true;
				this.updateCurColor(cX, cY);
				return false;
			},
			handleMouseDownB(ed){
				let cY = ed.offsetY;
				let ch = this.ch;
				cY = this.$u.limitVal(cY, 0, ch);
				this.bY = cY;
				this.mousePressB = true;
				return false;
			},
			handleMouseMoveA(em){
				if(this.mousePressA){
					let ch = this.ch;
					let mX = em.offsetX;
					let mY = em.offsetY;
					mX = this.$u.limitVal(mX, 0, ch);
					mY = this.$u.limitVal(mY, 0, ch);
					this.pX = mX;
					this.pY = mY;
					this.updateCurColor(mX, mY);
				}
			},
			handleMouseMoveB(em){
				if(this.mousePressB){
					let mY = em.offsetY;
					let ch = this.ch;
					mY = this.$u.limitVal(mY, 0, ch);
					this.bY = mY;
				}
			},
			handleMouseUpA(){
				this.mousePressA = false;
			},
			handleMouseUpB(){
				this.mousePressB = false;
			},
			handleTouchStartA(et){
				let ch = this.ch;
				let [tX, tY] = this.getTouchpoint(et);
				tX = this.$u.limitVal(tX, 0, ch);
				tY = this.$u.limitVal(tY, 0, ch);
				this.pX = tX;
				this.pY = tY;
				this.updateCurColor(tX, tY);
				return false;
			},
			handleTouchStartB(et){
				let ch = this.ch;
				let tY = this.getTouchpoint(et)[1];
				tY = this.$u.limitVal(tY, 0, ch);
				this.bY = tY;
				return false;
			},
			handleTouchMoveA(em){
				let ch = this.ch;
				let [mX, mY] = this.getTouchpoint(em);
				mX = this.$u.limitVal(mX, 0, ch);
				mY = this.$u.limitVal(mY, 0, ch);
				this.pX = mX;
				this.pY = mY;
				this.updateCurColor(mX, mY);
				return false;
			},
			handleTouchMoveB(em){
				let ch = this.ch;
				let mY = this.getTouchpoint(em)[1];
				mY = this.$u.limitVal(mY, 0, ch);
				this.bY = mY;
				return false;
			},
			//输入框改变事件。由于输入框懒加载与uniapp不兼容，所以此处采用传统方法解决该问题
			changeR(e){
				let _r = parseInt(this.$refs.R.value);
				_r = this.$u.limitVal(_r, 0, 255);
				this.rgbR = _r;
				let hsb = this.rgb2hsb(_r, this.rgbG, this.rgbB);
				this.hsbH = hsb[0];
				this.hsbS = hsb[1];
				this.hsbV = hsb[2];
				this.rgbHex = this.$u.regColor(_r, this.rgbG, this.rgbB, 'num');
			},
			changeG(e){
				let _g = parseInt(this.$refs.G.value);
				_g = this.$u.limitVal(_g, 0, 255);
				this.rgbG = _g;
				let hsb = this.rgb2hsb(this.rgbR, _g, this.rgbB);
				this.hsbH = hsb[0];
				this.hsbS = hsb[1];
				this.hsbV = hsb[2];
				this.rgbHex = this.$u.regColor(this.rgbR, _g, this.rgbB, 'num');
			},
			changeB(e){
				let _b = parseInt(this.$refs.B.value);
				_b = this.$u.limitVal(_b, 0, 255);
				this.rgbB = _b;
				let hsb = this.rgb2hsb(this.rgbR, this.rgbG, _b);
				this.hsbH = hsb[0];
				this.hsbS = hsb[1];
				this.hsbV = hsb[2];
				this.rgbHex = this.$u.regColor(this.rgbR, this.rgbG, _b, 'num');
			},
			changeS(e){
				let _s = parseInt(this.$refs.S.value);
				_s = this.$u.limitVal(_s, 0, 100);
				this.hsbS = _s;
				let rgb = this.hsb2rgb(this.hsbH, _s, this.hsbV);
				this.rgbHex = this.$u.regColor(rgb[0], rgb[1], rgb[2], 'num');
			},
			changeV(e){
				let _v = parseInt(this.$refs.V.value);
				_v = this.$u.limitVal(_v, 0, 100);
				this.hsbV = _v;
				let rgb = this.hsb2rgb(this.hsbH, this.hsbS, _v);
				this.rgbHex = this.$u.regColor(rgb[0], rgb[1], rgb[2], 'num');
			},
			changeH(e){
				let _h = parseInt(this.$refs.H.value);
				_h = this.$u.limitVal(_h, 0, 359);
				this.hsbH = _h;
			},
			changeHEX(e){
				let _hex = this.$refs.HEX.value;
				let rgb = this.$u.toDec(_hex);
				let hsb = this.rgb2hsb(rgb[0], rgb[1], rgb[2]);
				this.hsbH = hsb[0];
				this.hsbS = hsb[1];
				this.hsbV = hsb[2];
			},
			handleCancel(){
				this.$emit('cancel');
			},
			handleConfirm(){
				this.$emit('confirm',this.rgbHex);
			}
		}
	}
</script>

<style scoped>
	@media screen and (max-width:750px){
		.modal-container{
			width:85%
		}
		.platter-box{
			width: 66vw;
			margin: 2.5vh auto
		}
		#platter {
			width: 55vw;
			height: 55vw;
		}
		#colorBar {
			width: 6vw;
			height: 55vw;
			margin-left: 2vw;
		}
		.viewer {
			width: 10vw;
		}
	}
	@media screen and (min-width:751px){
		.modal-container {
			width:500px;
		}
		.platter-box{
			width:390px;
			margin: 2.5vh auto;
		}
		#platter {
			width: 324px;
			height: 324px;
		}
		#colorBar {
			width: 43px;
			height: 324px;
			margin-left: 22px;
		}
		.viewer {
			width: 60px;
		}
	}
	.pixcell-mask{
		position:fixed;
		left: 0;
		top: 0;
		right:0;
		bottom:0;
		background: rgba(0, 0, 0, 0.5);
		z-index: 999;
		display: -webkit-flex;
		display: -moz-flex;
		display: -o-flex;
		display:flex;
		justify-content:center;
		align-items:center;
	}
	.modal-container{
		background: #fff;
		border-radius:24rpx;
		border: #000 4rpx solid;
		overflow: hidden;
		z-index: 1000;
		box-shadow: 4rpx 4rpx 0px #000;
	}
	.header{
		width:100%;
		height:6vh;
		background: #fff;
		color: #000;
		text-align:center;
		line-height: 6vh;
		border-bottom: #000 3rpx solid;
		font-weight: bold;
	}
	.main-ui{
		background: #fff
	}
	.platter-box{
		position: relative;
		touch-action: none;
	}
	#platter{
		background: #fff;
		touch-action: none;
		display: inline-block;
		z-index: 999;
		border: #000 3rpx solid;
		border-radius: 18rpx;
		overflow: hidden;
	}
	#colorBar{
		touch-action: none;
		background: #fff;
		display: inline-block;
		z-index: 999;
		border: #000 3rpx solid;
		border-radius: 12rpx;
		overflow: hidden;
	}
	.options-box{
		margin: 3vh 0;
		margin-left: 5vw;
		color: #000;
		font-size: 24rpx;
		font-weight: 500;
		display: --webkit-flex;
		display: flex;
		align-items: center;
	}
	.viewer{
		border: #000 4rpx solid;
		border-radius: 18rpx;
		height: 6vh;
		display: inline-block;
		overflow: hidden;
	}
	.viewer-up{
		height: 3vh;
	}
	.viewer-down{
		height: 3vh;
	}
	.params{
		display:inline-block;
		margin-top: -10px;
		margin-left: 5vw;
	}
	.option-text{
		display: inline-block;
		vertical-align: top;
	}
	.option-line{
		height:22px;
		line-height: 22px;
		overflow: wrap
	}
	.inpt{
		outline:none;
		font-size: 23rpx;
		width:61rpx;
		height:28rpx;
		color: #000;
		font-weight: 500;
		border: #000 2rpx solid;
		display: inline-block;
		vertical-align: top;
		margin: 0 10rpx;
		border-radius: 20rpx;
		text-align: center;
		margin-top: 4rpx;
	}
	.inpt:focus{
		border: #1890ff 1px solid;
	}
	.hex{
		width:56px
	}
	.footer-btn{
		border-top: #000 3rpx solid;
	}
	.btn{
		height: 90rpx;
		width: 49%;
		display: inline-block;
		text-align: center;
		line-height:90rpx;
		cursor: pointer;
	}
	.btn-cancel{
		color: #aaa;
