						var iY = Math.floor(y / g);
					}
				}
				return [iX, iY];
			},
			/**
			 * 绘制单个像素块
			 * @param {*} x
			 * @param {*} y
			 * @param {Array} data
			 */
			drawPixel(x, y, data){
				var isZoom = this.zoomData.status;
				var [iX, iY] = this.calcPtIndex(x, y);
				var c = this.penColor;
				//填充网格
				//判断，以节省不必要的绘制。
				if(data[iX][iY] != c){
					this.gridVisible = true;
					data[iX][iY] = c;
					if(isZoom){
						this.updateZoomData({data});
					}
					this.clearAll();
					this.redraw(data);
					return true;
				}else{
					return false;
				}
			},
			/**
			 * 清除单个像素块
			 * @param {*} x 
			 * @param {*} y
			 * @param {Array} data
			 */
			clearPixel(x, y, data){
				var isZoom = this.zoomData.status;
				var [iX, iY] = this.calcPtIndex(x, y);
				//判断当前网格位置是否有数据，若有，则删除
				if(data[iX][iY]){
					this.gridVisible = true;
					data[iX][iY] = '';
					if(isZoom){
						this.updateZoomData({data});
					}
					this.clearAll();
					this.redraw(data);
					return true;
				}else{
					return false;
				}
			},
			/**
			 * 更新几何图形的渲染
			 * @param {Array} data
			 **/
			updateGraph(data){
				this.gridVisible = true;
				var isZoom = this.zoomData.status;
				const graph = this.graph;
				var c = this.penColor;
				var l = this.paintData.length;
				var d = this.$u.deepcopy(this.paintData[l-1]);
				var stp = graph.start;
				var edp = graph.end;
				var res;
				switch(graph.type){
					case 'line':
						res = this.drawGraphLine(stp[0], stp[1], edp[0], edp[1]);
						break;
					case 'ellipse':
						res = this.drawEllipse(stp[0], stp[1], edp[0], edp[1]);
						break;
					case 'square':
						res = this.drawSquare(stp[0], stp[1], edp[0], edp[1]);
						break;
				}
				if(res.length){
					var pt = false;
					for(let i = 0;i < res.length;i++){
						if(d[res[i][0]][res[i][1]] != c){
							d[res[i][0]][res[i][1]] = c;
							pt = true;
						}
					}
					//不能直接将d赋值给data，需要保持原data参数引用地址不变
					for(let i = 0;i < data.length;i++){
						data[i] = d[i];
					}
					if(isZoom){
						this.updateZoomData({data});
					}
					this.clearAll();
					this.redraw(data);
					return pt;
				}
			},
			//重置graph对象
			clearGraph(){
				const {graph} = this;
				graph.status = false;
				//graph.type = 'line';
				graph.start = [];
				graph.end = [];
			},
			//点击图形绘制工具按钮执行的事件
			handleGraphBtnClick(){
				let {currentMode, showGraphSubmenu} = this;
				if(currentMode != 'graph'){
					//当前模式不是图形模式时，点击按钮仅切换到图形绘制模式，不显示子菜单
					this.currentMode = 'graph';
				}else{
					//当前模式为图形绘制模式时，再次点击按钮可弹出或关闭子菜单选项
					this.showGraphSubmenu = !showGraphSubmenu;
				}
				return false;
			},
			/**
			 * 绘制像素直线
			 * @param {int} x1 y1 x2 y2 起笔点和终止点的横纵序列
			 **/
			drawGraphLine(x1, y1, x2, y2){
				let dx = x2 - x1;
				let dy = y2 - y1;
				let res = [];
				if(dx > 0){
					for(let i = 0;i < dx;i++){
						res.push([x1+i, Math.round(y1+(i*dy/dx))]);
					}
				}else if(dx < 0){
					for(let i = 0;i < -dx;i++){
						res.push([x1-i, Math.round(y1-(i*dy/dx))]);
					}
				}
				if(dy > 0){
					for(let i = 0;i < dy;i++){
						res.push([Math.round(x1+(i*dx/dy)), y1+i]);
					}
				}else if(dy < 0){
					for(let i = 0;i < -dy;i++){
						res.push([Math.round(x1-(i*dx/dy)), y1-i]);
					}
				}
				res.push([x2, y2]);
				return res.noRepeat();
			},
			/**
			 * 绘制像素椭圆
			 * @param {int} x1 y1 x2 y2 起笔点和终止点的横纵序列
			 * */
			drawEllipse(x1, y1, x2, y2){
				const sqrt = Math.sqrt;
				const rd = Math.round;
				let dx = Math.abs(x2 - x1 + 1);
				let dy = Math.abs(y2 - y1 + 1);
				let xm = this.$u.findMaxAndMin([x1, x2])[1];
				let ym = this.$u.findMaxAndMin([y1, y2])[1];
				let a = dy / 2;
				let b = dx / 2;
				let fa = dy >> 1;
				let fb = dx >> 1;
				let res = [];
				//建立标准坐标系，先计算4分之1椭圆弧的经过点坐标
				for(let i = 0;i < b;i++){
					if(dx%2){
						res.push([i, Math.floor(a*sqrt(1-((i+0.5)/b)**2))]);
					}else{
						res.push([i, Math.floor(a*sqrt(1-((i+1)/b)**2))]);
					}
				}
				for(let j = 0;j < a;j++){
					if(dy%2){
						res.push([Math.floor(b*sqrt(1-((j+0.5)/a)**2)), j]);
					}else{
						res.push([Math.floor(b*sqrt(1-((j+1)/a)**2)), j]);
					}
				}
				res = res.noRepeat();
				//将椭圆弧镜像两次并再次去重得到整个椭圆
				let l = res.length;
				if(dx%2){
					for(let i = 0;i < l;i++){
						res.push([-res[i][0], res[i][1]]);
					}
				}else{
					for(let i = 0;i < l;i++){
						res.push([-res[i][0]-1, res[i][1]]);
					}
				}
				l = res.length;
				if(dy%2){
					for(let i = 0;i < l;i++){
						res.push([res[i][0], -res[i][1]]);
					}
				}else{
					for(let i = 0;i < l;i++){
						res.push([res[i][0], -res[i][1]-1]);
					}
				}
				res = res.noRepeat();
				//位移，得到最终的数据点
				for(let i = 0;i < res.length;i++){
					res[i][0] += fb + xm;
					res[i][1] += fa + ym;
				}
				return res;
			},
			/**
			 * 绘制矩形
			 * @param {int} x1 y1 x2 y2 在画布中任意指定两点的横纵序列
			 * */
			drawSquare(x1, y1, x2, y2){
				let [mX, iX] = this.$u.findMaxAndMin([x1, x2]);
				let [mY, iY] = this.$u.findMaxAndMin([y1, y2]);
				let l1 = this.drawGraphLine(iX, iY, mX, iY);
				let l2 = this.drawGraphLine(mX, iY, mX, mY);
				let l3 = this.drawGraphLine(iX, mY, mX, mY);
				let l4 = this.drawGraphLine(iX, iY, iX, mY);
				let res = l1;
				res.push(...l2);
				res.push(...l3);
				res.push(...l4);
				return res.noRepeat();
			},
			/**
			 * 计算当前坐标所在网格序号
			 * @param {number} x
			 * @param {number} y
			 */
			calcPtIndex(x, y){
				var zoomData = this.zoomData;
				var isZoom = zoomData.status;
				var g = this.g;
				if(this.isApp && this.operation == 'exact'){
					var iX = x;
					var iY = y;
				}else{
					if(isZoom){
						var r = zoomData.n;
						var zg = this.w / r;
						var iX = Math.floor(x / zg) + zoomData.zX;
						var iY = Math.floor(y / zg) + zoomData.zY;
					}else{
						var iX = Math.ceil(x / g) - 1;
						var iY = Math.ceil(y / g) - 1;
					}
				}
				return [iX, iY];
			},
			/**
			 * 手势模式下计算两个触点距离
			 * @param {Array} p1
			 * @param {Array} p2
			 * */
			calcPtDistance(p1, p2){
				if(p1.length == 2 && p2.length == 2){
					var [x1, y1] = p1;
					var [x2, y2] = p2;
					return Math.sqrt((x1-x2)**2 + (y1-y2)**2);
				}else{
					return 0;
				}
			},
			//清除画板
			clearAll(){
				const C = this.C;
				const $C = this.$C;
				let w = this.w;
				C.beginPath();
				C.clearRect(0, 0, w, w);
				$C.beginPath();
				$C.clearRect(0, 0, w, w);
			},
			/**
			 * 取色
			 * @param {number} x
			 * @param {number} y
			 * */
			getColor(x, y){
				var l = this.paintData.length;
				var isZoom = this.zoomData.status;
				var [ptX, ptY] = [this.ptX, this.ptY];
				if(!this.isApp){
					var [iX, iY] = this.calcPtIndex(x, y);
					var res = this.paintData[l-1][iX][iY];
				}else{
					var res = this.paintData[l-1][ptX][ptY];
				}
				if(!res){
					res = this.bgColor;
				}
				this.penColor = res;
			},
			/**
			 * 循环式种子填充算法（4-联通算法）
			 * @param {int} x
			 * @param {int} y
			 * @param {String} oldColor 
			 * @param {String} newColor 
			 * @param {Array} data
			 */
			handleFill(x, y, oldColor, newColor, data){
				const {d4, resolution} = this;
				let curColor = data[x][y];
				if(oldColor != newColor && oldColor == curColor){
					let tasks = [[x, y]];
					data[x][y] = newColor;
					while(tasks.length){
						let nextTasks = [];
						for(let i = 0;i < tasks.length;i++){
							let cx = tasks[i][0];
							let cy = tasks[i][1];
							for(let j = 0;j < d4.length;j++){
								let nx = cx + d4[j].x;
								let ny = cy + d4[j].y;
								if(nx < 0 || ny < 0 || nx >= resolution || ny >= resolution){
									//该坐标越界，跳过
									continue;
								}
								let nc = data[nx][ny];
								if(nc == oldColor){
									nextTasks.push([nx,ny]);
									data[nx][ny] = newColor;
								}
							}
						}
						tasks = nextTasks;
					}
				}else{
					return;
				}
			},
			/**
			 * 重绘渲染
			 * @param {Array} data
			 * */
			redraw(data){
				let that = this;
				let zoomData = this.zoomData;
				let w = this.w;
				let g = this.g;
				let resolution = this.resolution;
				const {C, $C, cacheCanvas} = this;
				if(this.gridVisible && (this.operation == 'exact' || !this.isApp) && this.pointerVisible){
					this.pointer.visible = true;
				}else{
					this.pointer.visible = false;
				}
				if(!data){
					var l = this.paintData.length;
					data = this.paintData[l - 1];
				}
				//在缩放状态和正常视图状态下，采用不同方式重绘。
				//var starttime = Date.now();//用于计算下方代码执行一次消耗的时间
				if(zoomData.status){
					var r = zoomData.n;
					var zg = w / r;
					for(let i = 0;i < r;i++){
						for(let j = 0;j < r;j++){
							var x = i * zg;
							var y = j * zg;
							if(zoomData.data[i][j]){
								$C.fillStyle = zoomData.data[i][j];
								$C.fillRect(x, y, zg, zg);
							}
						}
					}
				}else{
					for(let i = 0;i < resolution;i++){
						for(let j = 0;j < resolution;j++){
							var x = i * g;
							var y = j * g;
							if(data[i][j]){
								$C.fillStyle = data[i][j];
								$C.fillRect(x, y, g, g);
							}
						}
					}
				}
				C.drawImage(cacheCanvas, 0, 0);
				//var endtime = Date.now();
				//this.debugInfo = endtime - starttime;
				return true;
			},
			//撤销
			undo(){
				let {paintData, toRedo} = this;
				let l = paintData.length;
				if(l > 1){
					toRedo.push(paintData[l - 1]);
					paintData.pop();
					let data = paintData[l - 2];
					let oldR = data.length;
					const vals = [16, 32, 48, 64, 96];
					let index;
					for(let i = 0;i < vals.length;i++){
						if(oldR == vals[i]){
							index = i;
							break;
						}
					}
					this.handleZoom(1);
					this.updateZoomData();
					//当分辨率发生变化时，不要在这一步做渲染，直接修改分辨率即可
					this.resolution = oldR;
					this.clearAll();
					this.redraw(data);
				}
				this.clearSelector();
				this.showToast({
					title: '撤销',
					duration: 600
				})
				return false;
			},
			//重做
			redo(){
				let {paintData, toRedo} = this;
				let l = toRedo.length;
				if(l > 0){
					paintData.push(toRedo[l - 1]);
					toRedo.pop();
					l = paintData.length;
					let data = paintData[l - 1];
					const vals = [16, 32, 48, 64, 96];
					let r = data.length;
					let index;
					for(let i = 0;i < vals.length;i++){
						if(r == vals[i]){
							index = i;
							break;
						}
					}
					this.resolution = r;
					this.rIndex = index;
					this.handleZoom(1);
					this.updateZoomData();
					this.clearAll();
					this.redraw(data);
					data = null;
				}
				this.clearSelector();
				this.showToast({
					title: '重做',
					duration: 600
				})
				return false;
			},
			//色卡下拉菜单按钮事件
			colorCardPulldown(){
				//去除遮挡
				this.showGraphSubmenu = false;
				let {colorCard} = this;
				this.colorCard=!colorCard;
			},
			//隐藏工具栏子菜单
			hideSubmenus(){
				this.showGraphSubmenu = false;
				this.showSelectSubmenu = false;
				this.colorCard = false;
			},
			//打开画布分辨率选择弹窗
			handleResolutionChange(){
				this.showResChooser = true;
			},
			//更改画布分辨率并渲染
			resChange(e){
				this.showResChooser = false;
				let oldR = this.resolution;
				let newR = e;
				this.resolution = newR;
				//watch监听中因为需要避免冲突所以没有对数据进行操作，放在这里进行
				this.changeResolution(oldR, newR);
				this.clearAll();
				this.redraw();
			},
			//变更快捷颜色选择条
			updateColorBar(e){
				let dataset = e.currentTarget.dataset.value;
				dataset = dataset.split(',');
				this.curColorBar = dataset;
			},
			/**
			 * 动态调整画布分辨率
			 * @param {int} n  旧边长格子数
			 * @param {int} m  新边长格子数
			 */
			changeResolution(n, m){
				//调整分辨率之前先清除画布中所有的缩放状态
				this.handleZoom(1);
				this.clearSelector();
				let that = this;
				let paintData = this.paintData;
				let bgColor = this.bgColor;
				let l = paintData.length;
				//该函数不对原数据进行复制所以无需对原数据进行深拷贝。
				let oldData = paintData[l-1];
				let newData = this.createEmptyData(m);
				let k = n / m;
				let k2 = k * k;
				//缺省颜色为背景色
				let defColor = this.$u.toDec(bgColor);
				const rd = Math.round;
				//计算新数据中每个新格子所占旧格子的颜色份额
				if(k < 1){
					//格子数变多，网格变小
					for(let i = 0;i < m;i++){
						for(let j = 0;j < m;j++){
							var ix = k * i;
							var iy = k * j;
							var xf = Math.floor(ix);
							var yf = Math.floor(iy);
							var xkf = Math.floor(ix + k);
							var ykf = Math.floor(iy + k);
							//判断是否越界以免报错
							if(xkf >= n){
								xkf = n - 1;
							}
							if(ykf >= n){
								ykf = n - 1;
							}
							var color = '';
							if(xf == xkf && yf == ykf){
								//当前新格子完全包含在旧格子内
								color = oldData[xf][yf];
							}else if(xf != xkf && yf == ykf){
								//当前新格子跨越了旧网格的竖线，被左右分割，或该格子右边线与旧网格竖线重合
								if(xkf == ix + k){
									//判定新格子右边线与原网格竖线重合，此时按第一种情况处理
									color = oldData[xf][yf];
								}else{
									var pl = (xkf-ix) / k;
									var pr = (ix+k-xkf) / k;
									var cl = this.$u.toDec(oldData[xf][yf]);
									var cr = this.$u.toDec(oldData[xkf][yf]);
									//取加权平均颜色
									if(!cl && !cr){
										//均未取得有效数据，直接输出空串
										color = '';
									}else{
										cl = cl || defColor;
										cr = cr || defColor;
										var [nR, nG, nB] = [rd(cl[0]*pl+cr[0]*pr), rd(cl[1]*pl+cr[1]*pr), rd(cl[2]*pl+cr[2]*pr)];
										color = this.$u.regColor(nR, nG, nB, 'num');
									}
								}
							}else if(xf == xkf && yf != ykf){
								//当前新格子跨越了旧网格的横线，被上下分割，或该格子下边线与旧网格横线重合
								if(ykf == iy + k){
									color = oldData[xf][yf];
								}else{
									var pu = (ykf-iy) / k;
									var pd = (iy+k-ykf) / k;
									var cu = this.$u.toDec(oldData[xf][yf]);
									var cd = this.$u.toDec(oldData[xf][ykf]);
									if(!cu && !cd){
										color = '';
									}else{
										cu = cu || defColor;
										cd = cd || defColor;
										var [nR, nG, nB] = [rd(cu[0]*pu+cd[0]*pd), rd(cu[1]*pu+cd[1]*pd), rd(cu[2]*pu+cd[2]*pd)];
										color = this.$u.regColor(nR, nG, nB, 'num');
									}
								} 
							}else if(xf != xkf && yf != ykf){
								//当前新格子横跨了旧网格的交叉点，被分割成四部分，或该格子右边线和下边线均与旧网格线重合
								if(xkf == ix + k && ykf == iy + k){
									color = oldData[xf][yf];
								}else{
									var p1 = (xkf-ix)*(ykf-iy)/k2;
									var p2 = (ix+k-xkf)*(ykf-iy)/k2;
									var p3 = (xkf-ix)*(iy+k-ykf)/k2;
									var p4 = (ix+k-xkf)*(iy+k-ykf)/k2;
									var c1 = this.$u.toDec(oldData[xf][yf]);
									var c2 = this.$u.toDec(oldData[xkf][yf]);
									var c3 = this.$u.toDec(oldData[xf][ykf]);
									var c4 = this.$u.toDec(oldData[xkf][ykf]);
									if(!c1 && !c2 && !c3 && !c4){
										color = '';
									}else{
										c1 = c1 || defColor;
										c2 = c2 || defColor;
										c3 = c3 || defColor;
										c4 = c4 || defColor;
										var [nR, nG, nB] = [
											rd(c1[0]*p1+c2[0]*p2+c3[0]*p3+c4[0]*p4),
											rd(c1[1]*p1+c2[1]*p2+c3[1]*p3+c4[1]*p4),
											rd(c1[2]*p1+c2[2]*p2+c3[2]*p3+c4[2]*p4),
										];
										color = this.$u.regColor(nR, nG, nB, 'num');
									}
								}
							}
							newData[i][j] = color;
						}
					}
				}else if(k > 1){
					//格子数变少，网格变大
					for(let i = 0;i < m;i++){
						for(let j = 0;j < m;j++){
							var ix = k * i;
							var iy = k * j;
							var xf = Math.floor(ix);
							var yf = Math.floor(iy);
							var nx = Math.ceil(ix+k) - xf;
							var ny = Math.ceil(iy+k) - yf;
							var raw = new Array(nx);
							var col = new Array(ny);
							var color = '';
							for(let p = 0;p < nx;p++){
								raw[p] = 1;
							}
							for(let p = 0;p < ny;p++){
								col[p] = 1;
							}
							raw[0] = 1 - ix%1;
							col[0] = 1 - iy%1;
							raw[nx-1] = (ix+k)%1 || 1;
							col[ny-1] = (iy+k)%1 || 1;
							//生成各行列小方块权重的矩阵，并从原始数据中切片
							var sh = [];
							var sl = [];
							var keepEmpty = true;
							for(let p = 0;p < nx;p++){
								var sr = [];
								var slr = [];
								for(let q = 0;q < ny;q++){
									var d = oldData[xf+p][yf+q];
									sr.push(raw[p] * col[q] / k2);
									if(d){
										keepEmpty = false;
									}
									slr.push(d);
								}
								sh.push(sr);
								sl.push(slr);
							}
							if(!keepEmpty){
								//将空数据替换成背景色参与计算
								var sumR = 0, sumG = 0, sumB = 0;
								for(let p = 0;p < nx;p++){
									for(let q = 0;q < ny;q++){
										if(!sl[p][q]){
											sl[p][q] = bgColor;
										}
										var c = this.$u.toDec(sl[p][q]);
										sumR += c[0]*sh[p][q];
										sumG += c[1]*sh[p][q];
										sumB += c[2]*sh[p][q];
									}
								}
								sumR = rd(sumR);
								sumG = rd(sumG);
								sumB = rd(sumB);
								color = this.$u.regColor(sumR, sumG, sumB, 'num');
							}else{
								color = '';
							}
							newData[i][j] = color;
						}
					}
				}
				this.updateData(newData);
			},
			//切换更多选项中要显示的子菜单
			switchSubmenu(submenu){
				if(submenu != this.submenuToShow){
					this.submenuToShow = submenu;
				}else{
					this.submenuToShow = '';
				}
			},
			handleColorPixerLongtap(){
				let that = this;
				this.colorPickerTimer = setTimeout(() => {
					that.colorCard = true;
				},780);
				return false;
			},
			colorPixerClearTimer(){
				if(this.colorPickerTimer){
					clearTimeout(this.colorPickerTimer);
				}
				this.colorPickerTimer = null;
			},
			beforeNavigateBack(){
			},
			//重命名
			handleRename(res){
				this.showRename = false;
				this.showToast({
					title: '重命名成功',
					duration: 1300
				})
				this.artworkName = res;
			},
			//展示小型消息弹窗
			showToast(opt){
				this.switchSubmenu('');
				this.toast.visible=false;
				if(!opt || typeof(opt) != 'object'){
					return;
				}
				let {title, duration} = opt;
				if(!duration){
					duration = 2500;
				}
				if(!title){
					title = '成功';
				}
				let toast = {
					title,
					visible: true
				}
				this.toast = toast;
				let that = this;
				let tm = setTimeout(() => {
					that.toast = {
						title: '',
						visible: false
					}
					clearTimeout(tm);
				}, duration)
			},
			//调用云函数接口在线保存
			saveImage(){
				this.switchSubmenu('');
				let {oCanvas, paintData, userId, artworkId, token} = this;
				let artworkName = this.artworkName.trim() || '无标题';
				//使用scale参数的目的是控制base64图片数据的大小，因为云函数的event参数大小限制100kb
				html2canvas(oCanvas,{scale: 0.8}).then(cvs => {
					//回调地狱
					let dataUrl = cvs.toDataURL('img/png');
					let l = paintData.length;
					let pd = paintData[l-1];
					if(!userId || !token){
						this.showToast({
							title: '获取用户信息失败，返回小程序后自动保存',
							duration: 1000
						});
						wx.miniProgram.postMessage({
							data:{
								artworkName: this.artworkName,
								pixcellData: pd,
								dataUrl
							}
						})
					}else{
						let isNew = artworkId ? false : true;
						let fileName = Date.now() + '';
						let rd = Math.random()+'';
						rd = rd.slice(2,8);
						let filePath = 'artworkThumbs/' + fileName + rd + '.png';
						this.$api.callFunction({
							name: 'uploadImage',
							method: 'POST',
							data: {
								path: filePath,
								base64: dataUrl
							}
						}).then(res => {
							let {fileID} = res.data;
							if(fileID){
								let artworkImgUrl = 'https://7463-tcb-skr4b0ax987338-2du4mbaca713d-1310200134.tcb.qcloud.la/' + filePath;
								this.$api.callFunction({
									name: 'updateArtworkInfo',
									method: 'POST',
									data: {
										token,
										artworkName,
										arrData: pd,
										userId,
										artworkImgUrl,
										artworkId,
										isNew
									}
								}).then(res => {
									this.showToast({
										title: '保存成功',
										duration: 1300
									})
									let {isNew, artworkId} = res.data;
									if(isNew && artworkId){
										this.artworkId = artworkId;
									}
								}).catch(err => {
									console.log(err);
									this.showToast({
										title: '保存失败',
										duration: 1300
									})
								})
							}
						}).catch(err => {
							this.showToast({
								title: '保存失败',
								duration: 1300
							})
							console.log(err);
						})
					}
				}).catch(err => {
					this.showToast({
						title: '图片转码失败',
						duration: 1300
					})
					console.log(err);
				})
			},
			//退出按钮事件函数
			handleQuit(){
				this.showWarning = true;
			},
			//退出画板页
			navigateBack(){
				wx.miniProgram.navigateBack({
					delta: 1
				});
			},
			//打开帮助
			openHelp(){
				this.switchSubmenu('');
				this.seeMore = false;
				this.showHelper = true;
			},
			//单次放大
			zoomIn(){
				let nk = this.calcZoomK(true);
				this.handleZoom(nk);
			},
			zoomOut(){
				let nk = this.calcZoomK(false);
				this.handleZoom(nk);
			},
			/**
			 * 初始化执行缩放的渲染（视野位置和光标有关系）
			 * @param {number} k 
			 */
			handleZoom(k){
				//返回值：true为实际执行了重绘渲染，false为未渲染，跳出函数
				let {ptX, ptY, zoomData, paintData} = this;
				if(k == zoomData.k){
					//缩放系数没有发生变化，没有执行缩放操作的必要。
					return false;
				}
				if(!k || k <= 1 || typeof(k) != 'number'){
					k = 1;
					zoomData.status = false;
					zoomData.k = 1;
					zoomData.n = null;
					zoomData.zX = 0;
					zoomData.zY = 0;
					zoomData.data = [];
					this.clearAll();
					this.redraw();
				}else{
					//通过当前pointer位置确定缩放后视野的左上数据点序列
					var r = this.resolution;//将原分辨率读取到函数中提升速度
					var zR = Math.ceil(r / (k*2));
					var nR = zR << 1;
					var zX = ptX - zR;
					var zY = ptY - zR;
					var zrX = ptX + zR - 1;
					var zrY = ptY + zR - 1;
					var l = paintData.length;
					var od = paintData[l-1];
					var d = [];
					//判断视野边界。若越界，对其进行限制
					if(zX < 0){
						zX = 0;
						zrX = nR - 1;
					}
					if(zY < 0){
						zY = 0;
						zrY = nR - 1;
					}
					if(zrX >= r){
						zrX = r - 1;
						zX = r - nR;
					}
					if(zrY >= r){
						zrY = r - 1;
						zY = r - nR;
					}
					//开始读取要渲染的数据
					for(let i = zX;i <= zrX;i++){
						var raw = [];
						for(let j = zY;j <= zrY;j++){
							raw.push(od[i][j]);
						}
						d.push(raw);
					}
					zoomData.status = true;
					zoomData.k = k;
					zoomData.n = nR;
					zoomData.zX = zX;
					zoomData.zY = zY;
					zoomData.data = d;
					this.clearAll();
					this.redraw(d);
				}
				//this.updateSelector(this.selector.rect);
				return true;
			},
			/**
			 * 更新缩放状态的数据（缩放系数不变时）
			 * @param {Object} opt 
			 */
			updateZoomData(opt){
				let zX, zY, data;
				let l = this.paintData.length;
				let zoomData = this.zoomData;
				if(opt){
					zX = opt.zX == undefined ? zoomData.zX : opt.zX;
					zY = opt.zY == undefined ? zoomData.zY : opt.zY;
					data = opt.data || this.paintData[l-1];
				}else{
					zX = zoomData.zX;
					zY = zoomData.zY;
					data = this.paintData[l-1];
				}
				zoomData.zX = zX;
				zoomData.zY = zY;
				var r = zoomData.n;
				//截取数据形成视野
				var d = [];
				for(let i = zX;i < r + zX;i++){
					var raw = [];
					for(let j = zY;j < r + zY;j++){
						raw.push(data[i][j]);
					}
					d.push(raw);
				}
				zoomData.data = d;
			},
			/**
			 * 根据当前分辨率设置和限制缩放系数(仅计算)
			 * @param {Boolean} u
			 */
			calcZoomK(u){
				let zoomData = this.zoomData;
				let zoom = this.zoom;
				let k = zoomData.k;
				let r = this.resolution;
				let nk;
				let l = zoom.length;
				if(u){
					for(let i = 0;i < l;i++){
						if(zoom[i] > k){
							nk = zoom[i];
							break;
						}else{
							nk = k;
						}
					}
					if(r <= 16 && nk > 2){
						nk = 2;
					}else if(r > 16 && r <= 48 && nk > 3){
						nk = 3;
					}else if(r > 48 && r <= 64 && nk > 4){
						nk = 4;
					}
				}else{
					for(let i = l - 1;i >= 0;i--){
						if(zoom[i] < k){
							nk = zoom[i];
							break;
						}else{
							nk = k;
						}
					}
				}
				return nk;
			},
			getMaxKPos(){
				let r = this.resolution;
				let nk;
				if(r <= 16){
					nk = 3;
				}else if(r > 16 && r <= 48){
					nk = 4;
				}else if(r > 48 && r <= 64){
					nk = 5;
				}else{
					nk = 6;
				}
				this.MaxKPos = nk;
				return nk;
			},
			//弹出调色板
			openPlatter(){
				this.popPlatter = true;
			},
			//关闭调色板，不从中获取颜色值
			closePlatter(){
				this.popPlatter = false;
			},
			//从调色板中监听到用户选择的新颜色值，并关闭调色板
			getNewColorByPlatter(e){
				this.penColor = e;
				this.popPlatter = false;
			},
			//电脑端的键盘事件
			handleKeyDown(e){
				let a = event.keyCode;
				if(a == 90 && event.ctrlKey){
					//Ctrl+Z 撤销
					this.undo();
				}else if(event.altKey && a == 72){
					//Alt+H 切换填充模式
					this.currentMode = 'fill';
				}else if(a == 13){
					//按回车用于测试某些功能
					//this.handleZoom(4);
				}else if(event.ctrlKey && a == 77){
					//Ctrl+M 移动选区
				}
			},
			//测试方法，用于绘制图形
			drawGraph(mode){
				this.currentMode = 'graph';
				this.graph.status = true;
				this.graph.type = mode;
			}
		}
	}
	export default Canvas
</script>

