import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
		version: '1.0.0',//pixcell版本号
		config: {
			appName: 'Pixcell_stand_alone',
			appLogo: '',
			appBeian: ''
		},
		sessionAuthId: '',
		userInfo: {},
		systemInfo: {},
		windowTop: 0,
		screenWidth: 0,
		screenHeight: 0,
		hasLogin: false,
		uuid: '',
		isApp: false
    },
    mutations: {
        initSysInfo(state){
			/* let res = uni.getSystemInfoSync();
			state.systemInfo = res;
			state.windowTop = res.windowTop; */
		},
		initIsApp(state){
			//const isApp = 'ontouchstart' in window;
			const isApp = true;
			state.isApp = isApp;
		},
		initSysFunc(){
			//专用于去除二维数组重复项的方法
			Array.prototype.noRepeat = function(){
				var o = this;
				if(!o.length){
					return [];
				}else{
					var n = [o[0]];
					for(let i = 1;i < o.length;i++){
						let isRepeat = true;
						for(let j = 0;j < n.length;j++){
							if(o[i].length == n[j].length){
								for(let k = 0;k < o[i].length;k++){
									if(o[i][k] != n[j][k]){
										isRepeat = false;
										break;
									}
								}
							}
							if(isRepeat){
								break;
							}
						}
						if(isRepeat){
							continue;
						}
						n.push(o[i]);
					}
					return n;
				}
			}
		},
		//更新设备屏幕宽高信息
		updateScreenSize(state){
			state.screenWidth = document.documentElement.clientWidth;
			state.screenHeight = document.documentElement.clientHeight;
		}
    },
    actions: {

    },
    getters: {
        
    }
})

export default store