<template>
    <div class="radio-outer-box">
        <div class="radio-container" :style="{'borderColor':status?'#000':'#bbb'}">
            <div class="radio-pointer" :style="{'background':status?'#000':'#fff'}"></div>
        </div>
    </div>
</template>

<script>
export default {
    //selected: 是否已被选中，可传入初始值
    props: {
        selected:{}
    },
    data() {
        return {
            status: false
        };
    },
    watch:{
        selected:function(v){
            this.status = v;
        },
    },
    mounted(){
        this.initRadio();
    },
    methods: {
        initRadio(){
            let v = this.selected;
            if(!v || v == '0' || v == 'false'){
                this.status = false;
            }else{
                this.status = true;
            }
        }
    }
}
</script>

<style scoped>
    .radio-outer-box {
        width: 28rpx;
        height: 28rpx;
    }
    .radio-container{
        width: 16rpx;
        height: 16rpx;
        border-width: 6rpx;
        border-style: solid;
        border-radius: 14rpx;
        display: flex;
        display: -webkit-flex;
        display: -moz-flex;
        display: -o-flex;
        align-items: center;
        justify-content: center;
        background: #fff;
        transition: all .4s
    }
    .switcher-pointer {
        width: 28rpx;
        height: 28rpx;
        border-width: 6rpx;
        border-style: solid;
        border-radius: 20rpx;
        background: #fff;
        position: absolute;
        transition: all .4s
    }
    .radio-pointer {
        width: 8rpx;
        height: 8rpx;
        border-radius: 4rpx;
    }
</style>