<template>
    <div class="switcher-container" @click="handleSwitch" :style="{'background':newValue?'#000':'#ccc'}">
        <div class="switcher-pointer" :style="{'borderColor':newValue?'#000':'#ccc', 'left':newValue?'5.33333333vw':'0'}"></div>
    </div>
</template>

<script>
export default {
    //type: 菜单类型，menu为普通菜单，sub为二级子菜单，默认为menu
    props: {
        value:{}
    },
    data() {
        return {
            newValue: false
        };
    },
    watch:{
        value:function(v){
            this.newValue = v;
        },
        newValue:function(v){
            this.$emit('input', v);
        }
    },
    mounted(){
        this.initSwitcher();
    },
    methods: {
        handleSwitch(){
            let v = this.newValue;
            this.newValue = !v;
        },
        initSwitcher(){
            let v = this.value;
            if(!v || v == '0' || v == 'false'){
                this.newValue = false;
            }else{
                this.newValue = true;
            }
        }
    }
}
</script>

<style scoped>
    .switcher-container{
        width: 80rpx;
        height: 40rpx;
        border-radius: 20rpx;
        position: relative;
        transition: background .4s
    }
    .switcher-pointer {
        width: 28rpx;
        height: 28rpx;
        border-width: 6rpx;
        border-style: solid;
        border-radius: 20rpx;
        background: #fff;
        position: absolute;
        transition: all .4s
    }
</style>