<template>
  <div class="submenu" @click.stop="handleClick">
	<span class="submenu-title">{{title}}</span>
    <slot></slot>
  </div>
</template>

<script>
export default {
  //type: 菜单类型，menu为普通菜单，sub为二级子菜单，默认为menu
  props:{
	type: {
		type: String
	},
	name: {
		type: String
	}
  },
  data() {
    return {
		title:''
    };
  },
  mounted(){
	this.title = this.name;
  },
  methods: {
	handleClick(e){
		this.$emit('click',e);
		return false;
	}
  }
}
</script>

<style scoped>
  .submenu{
	height: 84rpx;
	line-height: 84rpx;
	font-weight: 400;
	font-size: 28rpx;
  }
  .submenu-title {
	margin-left: 26rpx
  }
</style>