<template>
	<pix-mask level="999">
		<div class="warning-container">
			<div class="header">{{title}}</div>
            <div class="inputer-box">
                <input class="rename-inputer" type="text" v-model="artworkName" maxlength="10" />
                <div class="warn-content" v-show="showWarning">作品名称为空！</div>
            </div>
            <div class="footer-btns">
                <div class=" btn btn-cancel" @click.stop="handleCancel">取消</div>
				<div class="btn btn-confirm" @click.stop="handleConfirm">确定</div>
            </div>
		</div>
	</pix-mask>
</template>

<script>
	import pixMask from '../containers/pixcellMask.vue'
	export default {
		components:{
			pixMask
		},
		data(){
			return {
                title:'给作品起个好听的名字',
                artworkName: '',
                showWarning: false
			}
		},
		mounted(){
            let title = this.$attrs.title;
            if(title){
                this.artworkName = title;
            }
		},
        watch:{
            artworkName(n){
                if(!n || !n.trim()){
                    this.showWarning = true;
                }else{
                    this.showWarning = false;
                }
                return n;
            }
        },
		methods:{
			handleCancel(){
                this.$emit('cancel');
            },
            handleConfirm(){
                let {artworkName} = this;
                let n = artworkName.trim();
                if(artworkName && artworkName.trim()){
                    //作品名不为空时发起确定事件
                    this.$emit('confirm', n);
                }
            }
		}
	}
</script>

<style scoped>
	@media screen and (max-width:750px){
		/* 移动端 */
        .warning-container {
            width: 490rpx;
        }
	}
	@media screen and (min-width:751px){
		/* 电脑端 */
        .warning-container {
            width: 320px;
        }
	}
	.warning-container{
		background: #fff;
		border-radius:24rpx;
		border: #000 4rpx solid;
		overflow: hidden;
		z-index: 1100;
		box-shadow: 4rpx 4rpx 0px #000;
	}
    .header {
        width:100%;
		height:6vh;
		background: #fff;
		color: #000;
		text-align:center;
		line-height: 6vh;
		font-weight: bold;
    }
    .footer-btns{
		border-top: #000 3rpx solid;
	}
    .btn{
		height: 90rpx;
		width: 49%;
		display: inline-block;
		text-align: center;
		line-height:90rpx;
		cursor: pointer;
	}
	.btn-cancel{
		color: #aaa;
		border-right: #000 3rpx solid
	}
	.btn-confirm{
		color: #000;
		font-weight: 700
	}
    .inputer-box {
        width: 60%;
        margin: 65rpx auto;
    }
    .rename-inputer {
        width: 100%;
        display: block;
        height: 48rpx;
        outline: none
    }
    .warn-content {
        color: #f00;
        font-size: 24rpx;
        margin-top: 15rpx;
    }
</style>
