import axios from 'axios';

const accessToken = axios.create({
    baseURL: 'https://api.weixin.qq.com/',
    timeout: 20000
})

//跨域调用uniCloud云函数，需在云函数后端配置好允许CORS的开发测试域名和生产环境域名
const cloudFunction = axios.create({
    baseURL: 'https://tcb-skr4b0ax987338-2du4mbaca713d.service.tcloudbase.com/',
	timeout: 20000
})

const api = {
    getAccessToken(){
        return new Promise((resolve, reject) => {
            accessToken({
                url: 'cgi-bin/token?grant_type=client_credential&appid=wxd5a29436e781555c&secret=1d6618ceb75ea02f400b4f7169ac17c3',
                method: 'GET'
            }).then(res => {
                resolve(res);
            }).catch(err => {
                reject(err);
            })
        })
    },
    //调用uniCloud云函数
    callFunction(opt){
        let {name, method, data} = opt;
        let url = 'api/' + name;
        return new Promise((resolve, reject) => {
            cloudFunction({
                url,
                method,
                data
            }).then(res => {
                resolve(res);
            }).catch(err => {
                reject(err);
            })
        })
    }
}

export default api;