<template>
	<pix-mask @click="handleCancel" level="998">
        <warning v-if="showWarning" @confirm="handleConfirm" @cancel="handleWarningCancel">
            <div class="warning-content">
                <p>重新选择画布分辨率可能导致您的作品变形或失真！确定继续吗？</p>
                <p>（该操作可以撤销）</p>
            </div>
        </warning>
		<div class="choose-resolution-container">
			<div class="header">选择画布尺寸</div>
			<div class="chooser-item" v-for="(item, i) in options" :key="i" :data-value="item.value" @click.stop="changeResValue">
                <pixfont size="22" style="margin-left: 3vw">{{item.text}}</pixfont>
                <div class="radio-box">
                    <pix-radio :selected="index==i"></pix-radio>
                </div>
            </div>
            <div class="cancel-btn" @click.stop="handleCancel">取消</div>
		</div>
	</pix-mask>
</template>

<script>
    import pixfont from '../pixcellFont/pixfont.vue'
    import pixRadio from '../smallItems/pixcellRadio.vue'
    import warning from './warning.vue'
	import pixMask from '../containers/pixcellMask.vue'
	export default {
        components:{
            pixfont,
            pixRadio,
            warning,
			pixMask
        },
        props: {
            rIndex:{
                type: Number
            }
        },
		data(){
			return {
                options: [{
                    text: '16x16',
                    value: 16
                }, {
                    text: '32x32',
                    value: 32
                }, {
                    text: '48x48',
                    value: 48
                }, {
                    text: '64x64',
                    value: 64
                }, {
                    text: '96x96',
                    value: 96
                }],
                index: 0,
                showWarning: false,
                choosedValue: 16
			}
		},
		watch:{
			
		},
		mounted(){
            this.index = this.rIndex;
		},
		methods:{
			changeResValue(e){
                const {options} = this;
                let {index} = this;
                let val = e.currentTarget.dataset.value;
                val = parseInt(val);
                if(options[index].value != val){
                    //点击了新的分辨率，此时弹框警告
                    this.choosedValue = val;
                    this.showWarning = true;
                }
				return false;
            },
            handleConfirm(){
                this.showWarning = false;
                this.$emit('confirm', this.choosedValue);
				return false;
            },
            handleWarningCancel(){
                //点击警告框的取消后的取消事件，不是当前组件的取消事件
                this.showWarning = false;
				return false;
            },
            handleCancel(){
                //当前组件的取消事件
                this.$emit('cancel');
				return false;
            }
		}
	}
</script>

<style scoped>
	@media screen and (max-width:750px){
		/* 移动端 */
        .choose-resolution-container {
            width: 550rpx;
        }
	}
	@media screen and (min-width:751px){
		/* 电脑端 */
        .choose-resolution-container {
            width: 375px;
        }
	}
	.choose-resolution-container{
		background: #fff;
		border-radius:24rpx;
		border: #000 4rpx solid;
		overflow: hidden;
		z-index: 800;
		box-shadow: 4rpx 4rpx 0px #000;
	}
    .header {
        width:100%;
		height:6vh;
		background: #fff;
		color: #000;
		text-align:center;
		line-height: 6vh;
        font-size: 34rpx;
		font-weight: bold;
    }
    .chooser-item {
        width: 95%;
        height: 80rpx;
        margin: auto;
        border-bottom: rgba(0,0,0,0.1) 4rpx solid;
        display: flex;
        display: -webkit-flex;
        display: -moz-flex;
        display: -o-flex;
        align-items: center;
        justify-content: space-between;
		transition: background .3s
    }
	.chooser-item:active {
		background: #f4f4f4;
	}
    .radio-box {
        width: 20%;
        height: 80rpx;
        display: flex;
        display: -webkit-flex;
        display: -moz-flex;
        display: -o-flex;
        align-items: center;
        justify-content: center;
    }
    .warning-content {
        width: 90%;
        margin: auto;
        margin-bottom: 2vh;
        font-size: 28rpx
    }
    .cancel-btn {
        text-align: center;
        font-size: 34rpx;
        color: #999;
        height: 84rpx;
        line-height: 84rpx;
    }
</style>
